@import url(https://fonts.googleapis.com/css?family=Raleway:200);
#box {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  color: white;
  font-family: 'Raleway';
  font-size: 2rem;
}
.gradient-border {
  --borderWidth: 3px;
  background: #1D1F20;
  position: relative;
  border-radius: var(--borderWidth);
}
.gradient-border:after {
  content: '';
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
  border-radius: calc(2 * var(--borderWidth));
  z-index: -1;
  -webkit-animation: animatedgradient 3s ease alternate infinite;
          animation: animatedgradient 3s ease alternate infinite;
  background-size: 300% 300%;
}


@-webkit-keyframes animatedgradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


@keyframes animatedgradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}


.form-input {
  background-color: transparent;
  color: #fafafa;
  border: none;
  width: 80%;
  outline:none;
  border-bottom: 2px solid #ffffff;
  margin: auto;
}


.add-form {
	margin-bottom: 20px;
}

.item-add-form {
	position: relative;
}

.item-add-form::after {
	content: "";
	position: absolute;
	top: 40px;
	left: 18%;

	width: 80%;
	height: 1px;
	background-image: -webkit-gradient(linear, left bottom, right top, from(#47074a), color-stop(#64004a), color-stop(#7f0046), color-stop(#96003c), color-stop(#a9002f), color-stop(#b61827), color-stop(#c22b1d), color-stop(#cb3d0c), color-stop(#d55607), color-stop(#df6d05), color-stop(#e78207), to(#ee9811));
	background-image: linear-gradient(to right top, #47074a, #64004a, #7f0046, #96003c, #a9002f, #b61827, #c22b1d, #cb3d0c, #d55607, #df6d05, #e78207, #ee9811);
}

.submit-button {
	font-family: "Montserrat";
    font-size: 1.4em;
	color: #fafafa;
	width: 30%;
	height: 50px;
	background-color: transparent;
	border: none;
}

.add-form {
	background-color: transparent;
	color: #fafafa;
	border: none;
	width: 60%;
	outline:none;
	height: 55px;

}



.task-list {
  font-size: 1rem;
  font-weight: normal;
  color: #fafafa;
}

.task-item {
	width: 80%;
	margin-left: 0.1em;
    line-height: 25px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
     overflow-wrap: break-word;  /* не поддерживает IE, Firefox; является копией word-wrap */ 
  word-wrap: break-word;
  word-break: keep-all;  /* не поддерживает Opera12.14, значение keep-all не поддерживается IE, Chrome */ 
  line-break: loose;  /* нет поддержки для русского языка */ 
 -webkit-hyphens: none; -ms-hyphens: none; hyphens: none;  /* значение auto не поддерживается Chrome */ 
}

.task-list.done .task-item{
	text-decoration: line-through;
}

.list-group-item {
	background-color: #2d2d2d;
	
}
.clock-numb {
	font-family: "Montserrat";
	font-weight: bold; 
	background-image: url(https://media.giphy.com/media/26BROrSHlmyzzHf3i/giphy.gif);
    background-size: cover;
    color: transparent;
    -moz-background-clip: text;
    -webkit-background-clip: text;
    text-transform: uppercase;
    font-size: 8.1em;
    line-height: .75;
    margin: 10px 0;
}

.add-text {
    font-family: "Montserrat";
    line-height: 30px;
    font-size: 1.8em;
    width: 100%;
    text-align: center;
    overflow-wrap: break-word;  /* не поддерживает IE, Firefox; является копией word-wrap */ 
    word-wrap: break-word;
    word-break: keep-all;  /* не поддерживает Opera12.14, значение keep-all не поддерживается IE, Chrome */ 
    line-break: loose;  /* нет поддержки для русского языка */ 
    -webkit-hyphens: none; -ms-hyphens: none; hyphens: none;  /* значение auto не поддерживается Chrome */ 
    
}
